<template>
  <div class="map__wrapper">
    <l-map :zoom="zoom" :center="center">
      <l-tilelayer :url="url" :attribution="attribution"></l-tilelayer>
      <div v-if='showPrudesTrees'>
        <l-marker v-for="marker in prudesTrees" :key="marker.index" @click="markerClick(marker)"
        :lat-lng="{lat: marker.lat, lng: marker.lan}" :icon="prudesIcon"
      >
      </l-marker>
      </div>
      <div v-if='showNewTrees'>
          <l-marker v-for="marker in newTrees" :key="marker.index" @click="markerClick(marker)"
          :lat-lng="{lat: marker.lat, lng: marker.lan}" :icon="newIcon"
        >
        </l-marker>
      </div>
      <div v-if='showRemovedTrees'>
        <l-marker v-for="marker in removedTrees" :key="marker.index" @click="markerClick(marker)"
          :lat-lng="{lat: marker.lat, lng: marker.lan}" :icon="removedIcon"
        >
      </l-marker>
      </div>
    </l-map>
    <div class="info__container">
      <div class="info__checkbox info__wrapper">
        <span class="modal-window__terms">
              <app-checkbox
                v-model="checkBoxStatus"
                :value="status"
                :val="status"
                :checkboxId="status"
                :checkBoxText='status'
                textStyle='grey'
                v-for="status in statuses"
                :key="status"
              ></app-checkbox>
              <!-- {{checkBoxStatus}} -->
            </span>
      </div>
      <div class="info__wrapper" v-if="activeMarker">
        <p v-if="activeMarker.contractor">Підрядник: {{activeMarker.contractor}}</p>
        <p v-if="activeMarker.date">Дата проведення роботи: {{activeMarker.date}}</p>
        <p v-if="activeMarker.orderIdentifier">Номер замовлення: {{activeMarker.orderIdentifier}}</p>
        <p v-if="activeMarker.sender">Замовник: {{activeMarker.sender}}</p>
        <p v-if="activeMarker.status">Статус виконання: {{activeMarker.status}}</p>
        <p v-if="activeMarker.type">Дерево: {{activeMarker.type}}</p>
      </div>
    </div>
    
  </div>
</template>

<script>
import {mapState} from 'vuex';

import {LMap, LTileLayer, LMarker } from 'vue2-leaflet';
import L from 'leaflet';
import Checkbox from './ui/Checkbox.vue'

// <div>Icons made by <a href="https://www.flaticon.com/authors/freepik" title="Freepik">Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a></div>

export default {
  data() {
    return {
      zoom: 8,
      center: L.latLng(48, 33),
      url:'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
      attribution:'&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      newIcon: L.icon({
        iconUrl: require('@/assets/new.png'),
        iconSize: [24, 24],
        iconAnchor: [12, 12],
      }),
      removedIcon: L.icon({
        iconUrl: require('@/assets/stump.png'),
        iconSize: [24, 24],
        iconAnchor: [12, 12],
      }),
      prudesIcon: L.icon({
        iconUrl :require('@/assets/prun.png'),
        iconSize: [24, 24],
        iconAnchor: [12, 12],
      }),
      activeMarker: {},
      statuses: ["Нові дереві", "Обрізані дерева", "Видалені дерева"],
      checkBoxStatus: ["Нові дереві", "Обрізані дерева", "Видалені дерева"],
      showPrudesTrees: true,
      showNewTrees: true,
      showRemovedTrees: true
    }
  },
  components: {
    'l-map': LMap,
    'l-tilelayer' :LTileLayer,
    'l-marker': LMarker,
    'app-checkbox': Checkbox
  },
  mounted() {
    setTimeout(function() { window.dispatchEvent(new Event('resize')) }, 250);
  },
  computed:
    mapState({
      prudesTrees ({ecology}) {
        return ecology.prunedTreesArray
      },
      newTrees ({ecology}) {
        return ecology.newTreesArray
      },
      removedTrees ({ecology}) {
        return ecology.removedTreesArray
      },
    }),
  methods: {
    markerClick (marker) {
      console.log(marker, 'marker')
      this.activeMarker = marker
    }
  },
  watch: {
    checkBoxStatus: function (val) {
      if (val.indexOf("Нові дереві") === -1) {
        this.showNewTrees = false
      } else this.showNewTrees = true
      if (val.indexOf("Обрізані дерева") === -1) {
        this.showPrudesTrees = false
      } else this.showPrudesTrees = true
      if (val.indexOf("Видалені дерева") === -1) {
        this.showRemovedTrees = false
      } else this.showRemovedTrees = true
    },
  },
}
</script>
<style lang="scss">
@import "~leaflet/dist/leaflet.css";
</style>
<style lang="scss" scoped>
.map {
  &__wrapper {
    width: 100%;
    height: 500px;
  }
}
.info {
  &__wrapper {
    padding: 50px;
  }
  &__checkbox {
    display: flex;
    width: max-content;
  }
  &__container {
    display: flex;
    flex-wrap: wrap;
  }
}

</style>